import React, { useLayoutEffect, Fragment } from 'react';
import { motion } from 'framer-motion';
import { productDetails } from '../../utils/exportContent';
import { Link } from 'react-router-dom';
import Footer from './Footer';
import Tilt from 'react-parallax-tilt';

const fadeUpVariant = {
    initial: {
        opacity: 0,
        y: 20
    },
    final: {
        opacity: 1,
        y: 0,
        transition: {
            type: 'tween',
            duration: 1,
            delay: 0.5
        }
    }
};
const contentStyle = {
    "@media only screen (max-width:680px)": {
        marginTop: '10px'
    },
}

function ProductPage({ product }) {

    useLayoutEffect(() => {
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
    }, [])

    return (
        <Fragment>
            <div className='products-page-primary-container'>
                <div className='product-main-content-container'>
                    <div className='product-container'>
                        <motion.div
                            style={{ position: 'relative' }}
                            variants={fadeUpVariant}
                            initial='initial'
                            animate='final'
                        >
                            <h1>{product}</h1>
                            <Link className='back-link' to='/'>
                                <svg
                                    viewBox="0 0 100 46"
                                    fill="none"
                                    xmlns="http://www.w3.org/2000/svg"
                                >
                                    <path d="M98.5401 25.1213C99.7116 23.9497 99.7116 22.0503 98.5401 20.8787L79.4482 1.7868C78.2766 0.615224 76.3771 0.615224 75.2055 1.7868C74.034 2.95837 74.034 4.85786 75.2055 6.02944L92.1761 23L75.2055 39.9706C74.034 41.1421 74.034 43.0416 75.2055 44.2132C76.3771 45.3848 78.2766 45.3848 79.4482 44.2132L98.5401 25.1213ZM0.385681 26L96.4187 26V20L0.385681 20L0.385681 26Z" fill="#fff" />
                                </svg>
                            </Link>
                        </motion.div>

                        <motion.div
                            variants={fadeUpVariant}
                            initial='initial'
                            animate='final'
                        >
                            {productDetails[product].contentIntro}
                        </motion.div>
                    </div>
                    <Tilt perspective='5000'>
                        <div className='product-image'>
                            <img
                                style={{ width: "80%" }}
                                src={productDetails[product].image}
                                alt={productDetails[product].image}

                            />
                        </div>
                    </Tilt>
                    <div className='content'>
                        <div>{
                            productDetails[product].extra && <div className='extra'>
                                <p>{productDetails[product].extra[0]}</p>
                                <div>
                                    <a style={{ marginTop: '0.7em' }}>{productDetails[product].extra[1]}&nbsp;</a>
                                    <a className='extra'
                                        target="_blank"
                                        style={{ marginTop: '0.7em' }}
                                        rel="noopener noreferrer"
                                        href={productDetails[product].extra[2]}
                                    >
                                        {productDetails[product].extra[3]}
                                    </a>
                                </div>
                            </div>
                        }</div>
                        <section style={contentStyle}>{productDetails[product].content}</section>
                    </div>
                </div>
            </div>
            <div className='products-bottom-container'>
                <div className='link_container'>
                    <Link
                        className='next-link'
                        to={productDetails[product].nextLink.link}
                    >
                        {productDetails[product].nextLink.name} {' '}
                        <svg
                            viewBox="0 0 100 46"
                            fill="none"
                            xmlns="http://www.w3.org/2000/svg"
                        >
                            <path d="M98.5401 25.1213C99.7116 23.9497 99.7116 22.0503 98.5401 20.8787L79.4482 1.7868C78.2766 0.615224 76.3771 0.615224 75.2055 1.7868C74.034 2.95837 74.034 4.85786 75.2055 6.02944L92.1761 23L75.2055 39.9706C74.034 41.1421 74.034 43.0416 75.2055 44.2132C76.3771 45.3848 78.2766 45.3848 79.4482 44.2132L98.5401 25.1213ZM0.385681 26L96.4187 26V20L0.385681 20L0.385681 26Z" fill="#fff" />
                        </svg>
                    </Link>
                </div>
            </div>
            <Footer />
        </Fragment>
    )
};

export default ProductPage;
